@import "@/styles/main.scss";



















.mail-icon-container{
    background: #ffffff;
    cursor: pointer;
    width: 46px;
    height: 46px;
    position: fixed;
    border-radius: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 90%;
    left: 90%;
    z-index: 10;
    transition: all .1s ease;
    border: rgba($color: #000000, $alpha: 0.2) 1px solid;
    opacity: 0.5 ;

    .icon{
      color: #000000;
      width: 22px;
      height: 22px;
      font-weight: lighter !important;
      font-size: 22px;
      line-height: 2em;
      transition: all 0.1s ease;

    }

    &:hover{
        // transition: width 1s;
        background-color: #FF5D73;
        transform: scale(1.1) !important;

      .icon{
        color: #ffffff !important;
        transform: scale(1.03);

      }
        .envelope{
          fill: #ffffff;
        }
    }

}

@media (max-width: 1270px){
  .mail-icon-container{
    left: 89% !important;
  }
}
@media (max-width: 1000px){
  .mail-icon-container{
    left: 86% !important;
  }
}

@media (max-width: 850px){
  .mail-icon-container{
    left: 85% !important;
  }
}

@media (max-width: 720px){
  .mail-icon-container{
    left: 82% !important;
  }
}

@media (max-width: 650px){
  .mail-icon-container{
    left: 81% !important;
  }
}

@media (max-width: 550px){
  .mail-icon-container{
    left: 77% !important;
  }
}
@media (max-width: 500px){
  .mail-icon-container{
    left: 70% !important;
  }
}
