@import "@/styles/main.scss";


































































































































.contact-modal{
    position: fixed;
    display: none;
    right: 5%;
    bottom: 75px;
    width: 350px;
    height: 361px;
    z-index: 1000000;
    padding: 30px;
    border-radius: 4px;
    background-color: #ffffff;
    box-shadow: hsla(0, 0%, 0%, 0.12) 0px 0px 20px 0px;

    form{
        flex-direction: column;
        width: 100%;
        justify-content: space-between;
        align-items: flex-start;
        height: 311px;
        z-index: 1000000;


        input{
            width: 100%;
            border: 1px solid hsla(0, 0%, 65%, 0.4);
            border-radius: 5px;
            background-color: transparent;
            padding: 12px;
            font-size: 14px;

            &:focus{
                outline: none;
                border-bottom: 2px solid #225E9B;
                animation-name: pulse;
                animation-duration: 0.3s;

                &::placeholder{
                    color: #225E9B !important;
                    -webkit-transform: translateX(10px);
                    -moz-transform: translateX(10px);
                  }
            }

            &::-webkit-input-placeholder{
                transition: all 0.3s ease;
                color: #101010 ;
                font-weight: 200 ;
                font-size: 0.9rem;
            }

            &::-ms-input-placeholder{
                transition: all 0.3s ease;
                color: #101010 !important;
                font-weight: 200 !important;
                font-size: 0.9rem;
            }
        }

        textarea{
            width: 100%;
            border: 1px solid hsla(0, 0%, 65%, 0.4);
            border-radius: 5px;
            background-color: transparent;
            padding: 12px;
            font-size: 14px;

            &:focus{
                outline: none;
                border-bottom: 2px solid #225E9B;
                animation-name: pulse;
                animation-duration: 0.3s;
                &::placeholder{
                    color: #225E9B !important;
                    -webkit-transform: translateX(10px);
                    -moz-transform: translateX(10px);
                  }
            }

            &::-webkit-input-placeholder{
                transition: all 0.3s ease;
                color: #101010 ;
                font-weight: 200 ;
                font-size: 0.9rem;
            }

            &::-ms-input-placeholder{
                transition: all 0.3s ease;
                color: #101010 !important;
                font-weight: 200 !important;
                font-size: 0.9rem;
            }
        }

        .button-holder{
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;

            .cancel-button{
                border: none;
                background-color: transparent;
                width: 80px;
                outline: none;
                display: flex;
                justify-content: center;
                align-items: center;
                padding: 10px 15px;
                border: solid 1px #FD5D73;
                border-radius: 4px;
                color: #FD5D73;
                font-size: 1rem;
                transition: all 0.3s ease-in-out;
                font-weight: 600;

                &:hover{
                    width: 120px;
                    background-color: #FD5D73;
                    color: #ffffff;
                }
            }

            .send-button{
                width: 105px;
                background-color: transparent;
                letter-spacing: 0.1rem;
                border: none;
                outline: none;
                display: flex;
                justify-content: center;
                align-items: center;
                padding: 10px 15px;
                border: 1px solid #225E9B;
                border-radius: 4px;
                color: #225E9B;
                font-size: 1rem;
                font-weight: 600;
                transition: all 0.3s ease-in-out;

                &:hover{
                    width: 140px;
                    background-color: #225E9B;
                    color: #ffffff;

                    .loading{
                        color: #ffffff !important;
                        font-size: 1.4rem !important;
                    }
                }
            }
        }
    }
}

.loading{
    color: #225E9B;
    font-size: 1.4rem;
    animation:
        loading-rotate 1s infinite both;

        @keyframes loading-rotate {
            0% {
                transform: rotateZ(0deg);
            }
            100% {
                transform: rotateZ(360deg);
            }
        }
}

@media (max-width: 450px){
    .contact-modal{
        right: 2% !important;
    }
}

@media (max-width: 380px){
    .contact-modal{
        right: 2% !important;
        width: 300px;
        // height: 320px;

    }
}
